<docs>
# ui-accordions-items

Accordions wrapper items. Must have accordion item inside as default slot to make this work.

Props for wrapper :
  `multiple` : can open multiple item at the same time (default behavior is only 1 active)

props for item : 
  `disable` : can't open it
  `open` : force open on page load.

</docs>

<script lang="ts">
import { defineComponent, provide, reactive, toRefs, readonly } from 'vue';

export interface Data {
  active: Array<number>,
}

const UiAccordions = defineComponent({
  name: "UiAccordions",
  props: {
    /** do we allow multiple items open or only one at a time ? */
    multiple: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const data: Data = reactive(
      {
        active: [],
      }
    );

    const updateActive = (id: number, disabled: boolean) => {
      if (disabled) return;

      // when there is no limitation, give us the possibility to have multiple open panel
      if (props.multiple) {
        if (data.active.indexOf(id) > -1) {
          data.active.splice(data.active.indexOf(id), 1);
        } else {
          data.active.push(id);
        }
        // if we are actually limited in our selection..
      } else if (data.active.indexOf(id) > -1) {
        data.active.splice(data.active.indexOf(id), 1);
      } else {
        data.active.shift();
        data.active.push(id);
      }
    }

    // provide data to children
    provide('updateActive', updateActive)
    provide('active', readonly(data.active))

    return {
      ...toRefs(data), //data
    };
  },
});
export default UiAccordions

</script>

<template>
  <ui-animation animation="list" class="UiAccordions">
    <!-- @slot Default - UiAccordionItems goes here -->
    <slot />
  </ui-animation>
</template>
